export enum SectionType { OPEN, OPTIONS }
export type InputMode = 'none' | 'text' | 'decimal' | 'numeric' | 'tel' | 'search' | 'email' | 'url';
export type BaseSect = { id: string, question: string, type: SectionType, mandatory?: boolean }
export type OpenRes = { type: SectionType.OPEN, inputMode?: InputMode }
export type OptionsRes = { type: SectionType.OPTIONS, options: { id: string, value: string }[] }
export type Section = BaseSect & (OpenRes | OptionsRes);
