
import { defineComponent, PropType } from "@vue/runtime-core";
import { Section, SectionType } from "@/model/Section";

export default defineComponent({
  emits: ["onSubmit"],

  props: {
    sections: {
      type: Array as PropType<Section[]>,
      required: true,
    },
    submitText: String,
  },

  data() {
    const res: { [key: string]: any } = {};
    const errors: { [key: string]: boolean } = {};
    return { res, errors };
  },

  computed: {
    sectionType: () => SectionType,
    submitActive() {
      for (const section of this.sections) {
        if (
          section.mandatory &&
          (!this.$data.res[section.id] ||
            this.$data.res[section.id].length === 0)
        ) {
          return false;
        } else if (this.errors[section.id]) {
          return false;
        }
      }
      return true;
    },
  },

  methods: {
    onSubmit() {
      this.$emit("onSubmit", this.$data.res);
    },
    parseInput(input: string, section: Section) {
      if (section.type === SectionType.OPEN && section.inputMode) {
        const type = section.inputMode;
        if (type === "numeric") {
          const res = parseInt(input);
          if (isNaN(res)) {
            this.errors[section.id] = true;
            return null;
          }
          this.errors[section.id] = false;
          return res;
        }
        if (type === "decimal") {
          const res = parseFloat(input);
          if (isNaN(res)) {
            this.errors[section.id] = true;
            return null;
          }
          this.errors[section.id] = false;
          return res;
        }
      }
      return input;
    },
  },
});
